export default "<template>\r\n  <div class=\"dialog-root\" v-if=\"show\" @click.self=\"show = false\">\r\n    <div class=\"dialog-view\">\r\n      <img\r\n        class=\"dialog-close\"\r\n        @click=\"close\"\r\n        src=\"@/assets/icon-a/close-black.png\"\r\n        alt=\"close\"\r\n      />\r\n      <div class=\"dialog-title\">\r\n        <span class=\"border\"></span>\r\n        <span>限量号设置</span>\r\n      </div>\r\n      <div class=\"dialog-content\">\r\n        <div class=\"line1\">\r\n          <div class=\"label\">订单号</div>\r\n          <div class=\"value\">{{formData.orderNo}}</div>\r\n        </div>\r\n        <div class=\"line2\">\r\n          <div class=\"label\">限量号设置</div>\r\n          <input class=\"value\" v-model=\"formData.limitedNo\" placeholder=\"请输入内容\" />\r\n        </div>\r\n      </div>\r\n      <div class=\"dialog-foot\">\r\n        <div class=\"dialog-confirm\" @click=\"confirm\">保存</div>\r\n        <div class=\"dialog-cancel\" @click=\"close\">取消</div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  props: {\r\n    dialogShow: {\r\n      value: Boolean,\r\n      default: () => false,\r\n    },\r\n    options: {\r\n      value: Object,\r\n      default: () => {},\r\n    },\r\n  },\r\n  data() {\r\n    return {\r\n      show: false,\r\n      formData: null,\r\n    };\r\n  },\r\n  watch: {\r\n    dialogShow: {\r\n      handler(newVal) {\r\n        this.show = newVal;\r\n      },\r\n      immediate: true,\r\n    },\r\n  },\r\n  created() {\r\n    this.formData = this.options.contentData;\r\n  },\r\n  methods: {\r\n    close() {\r\n      this.$emit(\"close\");\r\n    },\r\n    confirm() {\r\n      this.$emit(\"confirm\", this.formData);\r\n    },\r\n  },\r\n};\r\n</script>\r\n<style scoped lang=\"scss\">\r\n.dialog-root {\r\n  z-index: 99;\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(#202020, 0.8);\r\n\r\n  .dialog-view {\r\n    position: absolute;\r\n    top: 50%;\r\n    transform: translate(-50%, -50%);\r\n    left: 50%;\r\n    width: 518px;\r\n    min-height: 100px;\r\n    background: #ffffff;\r\n    border-radius: 4px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    padding: 18px;\r\n\r\n    .dialog-close {\r\n      cursor: pointer;\r\n      width: 18px;\r\n      height: 18px;\r\n      position: absolute;\r\n      right: 19px;\r\n      top: 19px;\r\n    }\r\n\r\n    .dialog-title {\r\n      width: 100%;\r\n      font-weight: 500;\r\n      font-size: 16px;\r\n      color: #1a1a1a;\r\n      display: flex;\r\n      align-items: center;\r\n\r\n      .border {\r\n        width: 4px;\r\n        height: 18px;\r\n        margin-right: 8px;\r\n        background-color: #f50537;\r\n        border-radius: 40px;\r\n      }\r\n    }\r\n\r\n    .dialog-content {\r\n      width: 100%;\r\n      padding: 45px 6px 34px;\r\n      text-align: left;\r\n\r\n      .line1 {\r\n        .label {\r\n          font-weight: 400;\r\n          font-size: 14px;\r\n          line-height: 22px;\r\n          color: #999999;\r\n        }\r\n\r\n        .value {\r\n          margin-top: 8px;\r\n          font-weight: 400;\r\n          font-size: 16px;\r\n          line-height: 24px;\r\n          color: #666666;\r\n        }\r\n      }\r\n\r\n      .line2 {\r\n        margin-top: 32px;\r\n        .label {\r\n          font-weight: 400;\r\n          font-size: 14px;\r\n          line-height: 22px;\r\n          color: #1a1a1a;\r\n        }\r\n        .value {\r\n          box-sizing: border-box;\r\n          width: 471px;\r\n          height: 37px;\r\n          margin-top: 8px;\r\n          padding: 0 10px;\r\n          background-color: #ffffff;\r\n          color: #999999;\r\n          border: none;\r\n          border: 1px solid #d9d9d9;\r\n          border-radius: 2px;\r\n        }\r\n\r\n        input:focus {\r\n            border-color: #f50537!important;\r\n        }\r\n      }\r\n    }\r\n\r\n    .dialog-foot {\r\n      width: 100%;\r\n      padding: 0 10px;\r\n      display: flex;\r\n      justify-content: flex-end;\r\n      align-items: center;\r\n\r\n      .dialog-confirm {\r\n        cursor: pointer;\r\n        margin-right: 20px;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        width: 112px;\r\n        height: 37px;\r\n        background-color: #f50537;\r\n        border-radius: 2px;\r\n        color: #ffffff;\r\n      }\r\n      .dialog-confirm:active {\r\n        background-color: #fa335c;\r\n        opacity: 0.95;\r\n      }\r\n\r\n      .dialog-cancel {\r\n        cursor: pointer;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        width: 112px;\r\n        height: 37px;\r\n        color: #000000;\r\n        border-radius: 2px;\r\n        border: 1px solid #dddddd;\r\n      }\r\n\r\n      .dialog-cancel:active {\r\n        color: #4a4a4a;\r\n        opacity: 0.95;\r\n      }\r\n    }\r\n  }\r\n}\r\n</style>\r\n";