export default "<template>\r\n  <div class=\"dialog-root\" v-if=\"show\">\r\n    <div class=\"dialog-view\">\r\n      <img class=\"dialog-icon\" src=\"@/assets/dialog-icon.png\" alt=\"icon\" />\r\n      <div class=\"dialog-title\">出错了</div>\r\n      <div class=\"dialog-content\">{{ content }}</div>\r\n      <div class=\"dialog-foot\">\r\n        <div class=\"dialog-confirm\" @click=\"confirm\">\r\n          我知道了{{ number ? `(${number}s)` : \"\" }}\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  props: {\r\n    dialogShow: {\r\n      value: Boolean,\r\n      default: () => false,\r\n    },\r\n  },\r\n  data() {\r\n    return {\r\n      show: false,\r\n      Interval: null,\r\n      number: 60,\r\n      content: \"弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容\",\r\n    };\r\n  },\r\n  watch: {\r\n    dialogShow: {\r\n      handler(newVal) {\r\n        this.show = newVal;\r\n        newVal && this.countDown();\r\n      },\r\n      immediate: true,\r\n    },\r\n  },\r\n  beforeDestroy() {\r\n    this.Interval && clearInterval(this.Interval);\r\n  },\r\n  methods: {\r\n    countDown() {\r\n      this.Interval && clearInterval(this.Interval);\r\n      this.number = 60;\r\n      this.Interval = setInterval(() => {\r\n        this.number = this.number - 1;\r\n        if (this.number <= 0) {\r\n          this.show = false;\r\n          clearInterval(this.Interval);\r\n        }\r\n      }, 1000);\r\n    },\r\n    close() {\r\n      this.show = false;\r\n      this.$emit(\"close\");\r\n    },\r\n    confirm() {\r\n      this.show = false;\r\n      this.$emit(\"confirm\");\r\n    },\r\n  },\r\n};\r\n</script>\r\n<style scoped lang=\"scss\">\r\n.dialog-root {\r\n  z-index: 99;\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n\r\n  .dialog-view {\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 50%;\r\n    width: 288px;\r\n    min-height: 179px;\r\n    transform: translate(-50%, -50%);\r\n    background: rgba(0, 0, 0, 0.95);\r\n    border-radius: 4px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    padding: 45px 30px 32px;\r\n\r\n    .dialog-close {\r\n      cursor: pointer;\r\n      width: 18px;\r\n      height: 18px;\r\n      position: absolute;\r\n      right: 19px;\r\n      top: 19px;\r\n    }\r\n\r\n    .dialog-icon {\r\n      width: 101px;\r\n      height: 101px;\r\n      margin-bottom: 20px;\r\n    }\r\n\r\n    .dialog-title {\r\n      font-family: \"PingFang SC\";\r\n      font-style: normal;\r\n      font-weight: 400;\r\n      text-align: center;\r\n      font-size: 18px;\r\n      line-height: 26px;\r\n      color: #ffffff;\r\n    }\r\n\r\n    .dialog-content {\r\n      display: inline;\r\n      text-align: left;\r\n      margin: 15px auto;\r\n      font-weight: 500;\r\n      font-size: 14px;\r\n      line-height: 22px;\r\n      color: #b3b3b3;\r\n    }\r\n\r\n    .dialog-foot {\r\n      width: 100%;\r\n      padding: 0 10px;\r\n      display: flex;\r\n      justify-content: center;\r\n      align-items: center;\r\n\r\n      .dialog-confirm {\r\n        cursor: pointer;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        width: 140px;\r\n        height: 37px;\r\n        border-radius: 2px;\r\n        color: #ffffff;\r\n        border: 1px solid #ffffff;\r\n      }\r\n      .dialog-confirm:active {\r\n        color: #4a4a4a;\r\n        opacity: 0.95;\r\n      }\r\n    }\r\n  }\r\n}\r\n</style>\r\n";