export default "<template>\r\n  <div class=\"dialog-root\" v-if=\"show\" @click.self=\"show = false\">\r\n    <div class=\"dialog-view\">\r\n        <img class=\"dialog-close\" @click=\"close\" src=\"@/assets/icon-a/close-black.png\" alt=\"close\">\r\n        <img class=\"dialog-icon\" src=\"@/assets/dialog-icon.png\" alt=\"icon\">\r\n      <div class=\"dialog-title\">温馨提示</div>\r\n      <div class=\"dialog-content\">{{ content }}</div>\r\n      <div class=\"dialog-foot\">\r\n        <div class=\"dialog-confirm\" @click=\"confirm\">保存</div>\r\n        <div class=\"dialog-cancel\" @click=\"close\">取消</div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  props: {\r\n    dialogShow: {\r\n      value: Boolean,\r\n      default: () => false,\r\n    },\r\n  },\r\n  data() {\r\n    return {\r\n      show: false,\r\n      content:\r\n        \"弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容\",\r\n    };\r\n  },\r\n   watch: {\r\n    dialogShow: {\r\n        handler(newVal) {\r\n            this.show = newVal;\r\n        },\r\n        immediate: true\r\n    }\r\n  },\r\n  methods: {\r\n    close() {\r\n      this.$emit(\"close\", false);\r\n    },\r\n    confirm() {\r\n      this.$emit(\"confirm\", true);\r\n    },\r\n  },\r\n};\r\n</script>\r\n<style scoped lang=\"scss\">\r\n.dialog-root {\r\n  z-index: 99;\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(#202020, 0.8);\r\n\r\n  .dialog-view {\r\n    position: absolute;\r\n    top: 50%;\r\n    transform: translate(-50%,-50%);\r\n    left:  50%;\r\n    width: 336px;\r\n    min-height: 197px;\r\n    background: #ffffff;\r\n    border-radius: 4px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    padding: 50px 30px 32px;\r\n\r\n    .dialog-close {\r\n        cursor: pointer;\r\n        width: 18px;\r\n        height: 18px;\r\n        position: absolute;\r\n        right: 19px;\r\n        top: 19px;\r\n    }\r\n\r\n    .dialog-icon {\r\n        width: 101px;\r\n        height: 101px;\r\n        margin-bottom: 10px;\r\n    }\r\n\r\n    .dialog-title {\r\n      font-family: \"PingFang SC\";\r\n      font-style: normal;\r\n      font-weight: 400;\r\n      text-align: center;\r\n      font-size: 18px;\r\n      line-height: 26px;\r\n      color: #1a1a1a;\r\n    }\r\n\r\n    .dialog-content {\r\n      //   width: 100%;\r\n      display: inline;\r\n      text-align: left;\r\n      margin: 15px auto;\r\n      font-weight: 500;\r\n      font-size: 14px;\r\n      line-height: 22px;\r\n      color: #b3b3b3;\r\n    }\r\n\r\n    .dialog-foot {\r\n      width: 100%;\r\n      padding: 0 10px;\r\n      display: flex;\r\n      justify-content: space-between;\r\n      align-items: center;\r\n\r\n      .dialog-confirm {\r\n        cursor: pointer;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        width: 112px;\r\n        height: 37px;\r\n        background-color: #f50537;\r\n        border-radius: 2px;\r\n        color: #ffffff;\r\n      }\r\n      .dialog-confirm:active {\r\n        background-color: #fa335c;\r\n        opacity: 0.95;\r\n      }\r\n\r\n      .dialog-cancel {\r\n        cursor: pointer;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        width: 112px;\r\n        height: 37px;\r\n        color: #000000;\r\n        border-radius: 2px;\r\n        border: 1px solid #dddddd;\r\n      }\r\n\r\n      .dialog-cancel:active {\r\n        color: #4a4a4a;\r\n        opacity: 0.95;\r\n      }\r\n    }\r\n  }\r\n}\r\n</style>\r\n";